.outer-container {
	width: 100vw;
	height: 100vh;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
	overflow: hidden;
}

.arena-container {
	height: 300%;
	display: flex;
	align-items: center;
	justify-content: center;
	flex-direction: column;
}

.arena {
	width: 512px;
	height: 512px;
	position: relative;
	background: #7b7b7b;
}

.controls {
	width: 100vw;
	height: 100%;
	background: #aaa;
	z-index: 100;
	display: flex;
	align-items: center;
	flex-direction: column;
	padding: 8px;
}
.controls button {
	width: 128px;
	border: none;
	padding: 8px 16px;
	margin: 4px;
	font-size: larger;
	border: 1px solid #8000ff;
	border-radius: 4px;
	background: #EEE;
}
.controls button:hover {
	background: #DDD;
	border: 1px solid #6000bf;
}
.controls button:active {
	background: #CCC;
	border: 1px solid #40007f;
}
.castbar {
	width: 384px;
	height: 32px;
	background: white;
}
.castbar-progress {
	height: 100%;
	background: red;
}
.ability-name {
	color: darkred;
	font-weight: bold;
	font-size: large;
}

.safe-marker {
	position: absolute;
	width: 48px;
	height: 48px;
	margin-left: -24px;
	margin-top: -24px;
	font-weight: bold;
	display: flex;
	align-items: center;
	justify-content: center;
	border-radius: 50%;
	cursor: pointer;
	user-select: none;
}
.safe-marker.pos-a {
	background: red;
	left: calc(256px - 76px);
	top: calc(256px - 128px);
}
.safe-marker.pos-a:hover {
	box-shadow: 0 0 3pt 2pt #bf0000;
}
.safe-marker.pos-a:active {
	box-shadow: 0 0 3pt 2pt #7f0000;
}
.safe-marker.pos-one {
	background: red;
	left: calc(256px - 128px);
	top: calc(256px - 76px);
}
.safe-marker.pos-one:hover {
	box-shadow: 0 0 3pt 2pt #bf0000;
}
.safe-marker.pos-one:active {
	box-shadow: 0 0 3pt 2pt #7f0000;
}
.safe-marker.pos-b {
	background: yellow;
	left: calc(256px + 76px);
	top: calc(256px - 128px);
}
.safe-marker.pos-b:hover {
	box-shadow: 0 0 3pt 2pt #bfbf00;
}
.safe-marker.pos-b:active {
	box-shadow: 0 0 3pt 2pt #7f7f00;
}
.safe-marker.pos-two {
	background: yellow;
	left: calc(256px + 128px);
	top: calc(256px - 76px);
}
.safe-marker.pos-two:hover {
	box-shadow: 0 0 3pt 2pt #bfbf00;
}
.safe-marker.pos-two:active {
	box-shadow: 0 0 3pt 2pt #7f7f00;
}
.safe-marker.pos-c {
	background: #87e5ed;
	left: calc(256px + 76px);
	top: calc(256px + 128px);
}
.safe-marker.pos-c:hover {
	box-shadow: 0 0 3pt 2pt #36d3e0;
}
.safe-marker.pos-c:active {
	box-shadow: 0 0 3pt 2pt #1896a1;
}
.safe-marker.pos-three {
	background: #87e5ed;
