	top: calc(256px + 76px);
}
.safe-marker.pos-three:hover {
	box-shadow: 0 0 3pt 2pt #36d3e0;
}
.safe-marker.pos-three:active {
	box-shadow: 0 0 3pt 2pt #1896a1;
}
.safe-marker.pos-d {
	background: #8000ff;
	left: calc(256px - 76px);
	top: calc(256px + 128px);
}
.safe-marker.pos-d:hover {
