.boss {
	position: absolute;
	left: 256px;
	top: 256px;
	margin-left: -96px;
	margin-top: -96px;
	border-radius: 50%;
}
.boss-image {
	width: 192px;
	height: 192px;
	background-image: url("./boss-e9.png");
	background-position: center;
	background-size: contain;
	border-radius: 50%;
	transform: rotate(10deg);
}
.boss.facing-0 {
	transform: rotate(-90deg);
}
.boss.facing-45 {
	transform: rotate(-45deg);
}
.boss.facing-90 {
	transform: rotate(0deg);
}
.boss.facing-135 {
	transform: rotate(45deg);
}
.boss.facing-180 {
	transform: rotate(90deg);
}
.boss.facing-225 {
	transform: rotate(135deg);
}
.boss.facing-270 {
	transform: rotate(180deg);
}
.boss.facing-315 {
	transform: rotate(225deg);
}

.clone {
	position: absolute;
	margin-left: -96px;
	margin-top: -96px;
	border-radius: 50%;
}
.clone-image {
	width: 192px;
	height: 192px;
	background-image: url("./boss-e9.png");
	background-position: center;
	background-size: contain;
	border-radius: 50%;
	transform: rotate(10deg);
	filter: sepia();
}
.clone.position-0 {
	left: 512px;
	top: 256px;
	transform: rotate(90deg);
}
.clone.position-45 {
	left: 512px;
	top: 512px;
	transform: rotate(135deg);
}
.clone.position-90 {
	left: 256px;
	top: 512px;
	transform: rotate(180deg);
}
.clone.position-135 {
	left: 0;
	top: 512px;
	transform: rotate(225deg);
}
.clone.position-180 {
	left: 0;
	top: 256px;
	transform: rotate(270deg);
}
.clone.position-225 {
	left: 0;
	top: 0;
	transform: rotate(315deg);
}
.clone.position-270 {
	left: 256px;
	top: 0;
	transform: rotate(0deg);
}
.clone.position-315 {
	left: 512px;
	top: 0;
	transform: rotate(45deg);
}

.boss-cleave {
	position: absolute;
	width: 64px;
	height: 64px;
	background: purple;
	border-radius: 50%;
	box-shadow: 0 0 3pt 2pt red;
	margin-left: -32px;
	margin-top: -32px;
}
.boss-cleave-left {
	left: calc(96px + 48px);
	top: 96px;
}
.boss-cleave-right {
	left: calc(96px - 48px);
	top: 96px;
}

.boss-cleave-reveal {
	position: absolute;
	width: 5000px;
	height: 5000px;
	background: #FF00FF50;
	margin-top: -2500px;
	pointer-events: none;
}
.boss-cleave-reveal.boss-cleave-left {
	margin-left: -48px;
}
.boss-cleave-reveal.boss-cleave-right {
	margin-left: calc(-5000px + 48px);
}
